"use client";

import { useEffect, useState } from "react";
import { motion } from "framer-motion";
import { useRouter } from "next/navigation";
import { useAuthStore } from "../store/authStore";
import { useProfileStore } from "../store/profileStore";
import { profileApi, reviewApi } from "../lib/api";
import { Review } from "../lib/types";
import {
    User, BarChart2, Clock, BookOpen, Star, AlertTriangle,
    Lightbulb, TrendingUp, TrendingDown, Database
} from "lucide-react";

const EXP_COLORS: Record<string, string> = {
    beginner: "bg-green-100 text-green-700",
    intermediate: "bg-yellow-100 text-yellow-700",
    advanced: "bg-purple-100 text-purple-700",
};

function SkillBar({ name, score }: { name: string; score: number }) {
    const pct = Math.round(score * 100);
    return (
        <div>
            <div className="flex justify-between text-xs mb-1">
                <span className="text-[#374151] font-medium">{name}</span>
                <span className={`font-semibold ${pct >= 70 ? "text-green-600" : pct >= 40 ? "text-yellow-600" : "text-red-600"}`}>
                    {pct}%
                </span>
            </div>
            <div className="h-2 bg-[#f3f4f6] rounded-full overflow-hidden">
                <motion.div
                    className={`h-full rounded-full ${pct >= 70 ? "bg-green-500" : pct >= 40 ? "bg-yellow-500" : "bg-red-500"}`}
                    initial={{ width: 0 }}
                    animate={{ width: `${pct}%` }}
                    transition={{ duration: 0.8, ease: "easeOut" }}
                />
            </div>
        </div>
    );
}

export default function ProfilePage() {
    const router = useRouter();
    const { isAuthenticated, user } = useAuthStore();
    const { profile, setProfile } = useProfileStore();
    const [reviews, setReviews] = useState<Review[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        if (!isAuthenticated) { router.push("/login"); return; }
        (async () => {
            try {
                const [profileRes, reviewsRes] = await Promise.all([
                    profile ? Promise.resolve({ data: profile }) : profileApi.get(),
                    reviewApi.history().catch(() => ({ data: [] })),
                ]);
                if (!profile) setProfile(profileRes.data);
                setReviews(reviewsRes.data?.slice(0, 10) || []);
            } catch {
                // silently handle
            } finally {
                setLoading(false);
            }
        })();
    }, [isAuthenticated, router, profile, setProfile]);

    if (loading) {
        return (
            <div className="min-h-screen flex items-center justify-center pt-16">
                <div className="w-8 h-8 border-2 border-[#f97316] border-t-transparent rounded-full animate-spin" />
            </div>
        );
    }

    const sortedSkills = Object.entries(profile?.skills || {}).sort(([, a], [, b]) => b - a);

    return (
        <div className="min-h-screen bg-[#faf9f6] pt-20">
            <div className="max-w-6xl mx-auto px-6 py-10">
                {/* Header */}
                <motion.div
                    className="flex items-start gap-5 mb-10"
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                >
                    <div className="w-16 h-16 bg-[#f97316] rounded-2xl flex items-center justify-center text-white text-2xl font-serif font-bold">
                        {user?.name?.[0]?.toUpperCase() || "U"}
                    </div>
                    <div>
                        <h1 className="font-serif font-black text-3xl md:text-4xl text-[#0d0d0d]">
                            {user?.name}
                        </h1>
                        <p className="text-[#9ca3af] text-sm mt-0.5">{user?.email}</p>
                        <div className="flex items-center gap-2 mt-2">
                            {profile?.experience_level && (
                                <span className={`text-xs font-semibold px-2.5 py-1 rounded-full ${EXP_COLORS[profile.experience_level]}`}>
                                    {profile.experience_level}
                                </span>
                            )}
                            {profile?.preferred_style && (
                                <span className="text-xs text-[#9ca3af] bg-[#f3f4f6] px-2.5 py-1 rounded-full">
                                    {profile.preferred_style} learner
                                </span>
                            )}
                        </div>
                    </div>
                </motion.div>

                {/* Stats row */}
                <motion.div
                    className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-8"
                    initial={{ opacity: 0, y: 10 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 0.1 }}
                >
                    {[
                        { icon: BookOpen, val: profile?.submissions_count || 0, label: "Submissions" },
                        { icon: Star, val: profile?.strengths?.length || 0, label: "Strengths" },
                        { icon: AlertTriangle, val: profile?.gaps?.length || 0, label: "Gaps" },
                        { icon: Database, val: sortedSkills.length, label: "Tracked Concepts" },
                    ].map((s, i) => (
                        <div key={i} className="bg-white border border-[#e5e7eb] rounded-xl p-5">
                            <s.icon className="w-4 h-4 text-[#f97316] mb-2" />
                            <div className="font-serif font-bold text-3xl text-[#0d0d0d]">{s.val}</div>
                            <div className="text-xs text-[#9ca3af]">{s.label}</div>
                        </div>
                    ))}
                </motion.div>

                <div className="grid lg:grid-cols-3 gap-6">
                    {/* Skills */}
                    <motion.div
                        className="lg:col-span-2 bg-white border border-[#e5e7eb] rounded-2xl p-6"
                        initial={{ opacity: 0, x: -20 }}
                        animate={{ opacity: 1, x: 0 }}
                        transition={{ delay: 0.15 }}
                    >
                        <div className="flex items-center gap-2 mb-5">
                            <BarChart2 className="w-4 h-4 text-[#f97316]" />
                            <h2 className="font-semibold text-sm text-[#374151] uppercase tracking-wider">Skill Breakdown</h2>
                        </div>
                        {sortedSkills.length > 0 ? (
                            <div className="grid sm:grid-cols-2 gap-x-8 gap-y-3">
                                {sortedSkills.map(([name, score]) => (
                                    <SkillBar key={name} name={name} score={score} />
                                ))}
                            </div>
                        ) : (
                            <p className="text-sm text-[#9ca3af]">Solve problems to populate your skill map.</p>
                        )}
                    </motion.div>

                    {/* Right col */}
                    <div className="flex flex-col gap-4">
                        {/* Strengths & Gaps */}
                        <motion.div
                            className="bg-white border border-[#e5e7eb] rounded-2xl p-5"
                            initial={{ opacity: 0, x: 20 }}
                            animate={{ opacity: 1, x: 0 }}
                            transition={{ delay: 0.2 }}
                        >
                            <div className="mb-4">
                                <div className="flex items-center gap-1.5 mb-2">
                                    <Star className="w-3.5 h-3.5 text-green-500" />
                                    <span className="text-xs font-bold text-green-600 uppercase tracking-wider">Strengths</span>
                                </div>
                                <div className="flex flex-wrap gap-1.5">
                                    {(profile?.strengths || []).map((s) => (
                                        <span key={s} className="text-xs bg-green-50 text-green-700 border border-green-100 px-2 py-0.5 rounded-full">{s}</span>
                                    ))}
                                    {!profile?.strengths?.length && <span className="text-xs text-[#9ca3af]">None yet</span>}
                                </div>
                            </div>
                            <div>
                                <div className="flex items-center gap-1.5 mb-2">
                                    <AlertTriangle className="w-3.5 h-3.5 text-red-500" />
                                    <span className="text-xs font-bold text-red-600 uppercase tracking-wider">Gaps</span>
                                </div>
                                <div className="flex flex-wrap gap-1.5">
                                    {(profile?.gaps || []).map((g) => (
                                        <span key={g} className="text-xs bg-red-50 text-red-700 border border-red-100 px-2 py-0.5 rounded-full">{g}</span>
                                    ))}
                                    {!profile?.gaps?.length && <span className="text-xs text-[#9ca3af]">None yet</span>}
                                </div>
                            </div>
                        </motion.div>

                        {/* Mistake patterns */}
                        {profile?.mistake_patterns && profile.mistake_patterns.length > 0 && (
                            <motion.div
                                className="bg-[#fef3c7] border border-yellow-200 rounded-2xl p-5"
                                initial={{ opacity: 0, x: 20 }}
                                animate={{ opacity: 1, x: 0 }}
                                transition={{ delay: 0.25 }}
                            >
                                <div className="flex items-center gap-1.5 mb-3">
                                    <AlertTriangle className="w-3.5 h-3.5 text-yellow-600" />
                                    <span className="text-xs font-bold text-yellow-700 uppercase tracking-wider">Watch Out For</span>
                                </div>
                                <ul className="flex flex-col gap-1">
                                    {profile.mistake_patterns.map((p, i) => (
                                        <li key={i} className="text-xs text-yellow-800">• {p}</li>
                                    ))}
                                </ul>
                            </motion.div>
                        )}

                        {/* Recent hints */}
                        {profile?.recent_hints && profile.recent_hints.length > 0 && (
                            <motion.div
                                className="bg-white border border-[#e5e7eb] rounded-2xl p-5"
                                initial={{ opacity: 0, x: 20 }}
                                animate={{ opacity: 1, x: 0 }}
                                transition={{ delay: 0.3 }}
                            >
                                <div className="flex items-center gap-1.5 mb-3">
                                    <Lightbulb className="w-3.5 h-3.5 text-[#f97316]" />
                                    <span className="text-xs font-bold text-[#f97316] uppercase tracking-wider">Recent Hints</span>
                                </div>
                                <ul className="flex flex-col gap-2">
                                    {profile.recent_hints.slice(0, 5).map((h, i) => (
                                        <li key={i} className="text-xs text-[#374151] border-l-2 border-[#f97316]/30 pl-2">{h}</li>
                                    ))}
                                </ul>
                            </motion.div>
                        )}
                    </div>
                </div>

                {/* Review history */}
                <motion.div
                    className="mt-6 bg-white border border-[#e5e7eb] rounded-2xl p-6"
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 0.35 }}
                >
                    <div className="flex items-center gap-2 mb-5">
                        <Clock className="w-4 h-4 text-[#f97316]" />
                        <h2 className="font-semibold text-sm text-[#374151] uppercase tracking-wider">Review History</h2>
                    </div>
                    {reviews.length === 0 ? (
                        <p className="text-sm text-[#9ca3af]">No reviews yet. Submit a solution to get started!</p>
                    ) : (
                        <div className="flex flex-col gap-3">
                            {reviews.map((r, i) => (
                                <div key={i} className="flex items-start gap-4 px-4 py-3 rounded-xl hover:bg-[#f9fafb] transition-colors">
                                    <div className="w-8 h-8 bg-[#f3f4f6] rounded-lg flex items-center justify-center shrink-0 mt-0.5">
                                        <BookOpen className="w-4 h-4 text-[#9ca3af]" />
                                    </div>
                                    <div className="flex-1 min-w-0">
                                        <p className="font-semibold text-sm text-[#0d0d0d]">{r.problem_title}</p>
                                        <p className="text-xs text-[#9ca3af] mt-0.5 truncate">
                                            {r.thinking_style} • {r.known_concepts?.join(", ") || "—"}
                                        </p>
                                    </div>
                                    <div className="flex items-center gap-1.5 text-xs text-[#9ca3af] whitespace-nowrap">
                                        <span className="uppercase text-[10px] font-mono bg-[#f3f4f6] px-2 py-0.5 rounded">{r.language}</span>
                                    </div>
                                </div>
                            ))}
                        </div>
                    )}
                </motion.div>
            </div>
        </div>
    );
}
